*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  line-height: 1.5;
  font-family: inherit;
  background-color: #fff;
  color: #000;
}

ul,
ol {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  background: none;
  border: none;
  outline: none;
  appearance: none;
}

img,
picture {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

.burger-open,
.burger-arrow {
  width: 55px;
  height: 55px;
}

.burger-arrow a:hover {
  transform: scale(1.1);
}

.burger-menu,
.burger-open,
.burger-close,
.burger-arrow {
  cursor: pointer;
  display: none;
}
.burger-menu:hover,
.burger-open:hover,
.burger-close:hover,
.burger-arrow:hover {
  transform: scale(1.1);
}

@media (max-width: 900px) {
  .burger-menu,
  .burger-open {
    display: flex;
    gap: 25px;
  }
}

.burger-open.active {
  display: none;
}

.burger-close.active {
  display: block;
}

