  background-color: white;
  box-shadow: 6px 5px 0px 0px #000, 0 0 0 2px #000;
  transition: ease 0.3s;
}
.menu_item-link--button:active {
  transform: translateY(10px);
}

.menu_item {
  position: relative;
  cursor: pointer;
}

.button-decoration {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.button-decoration:hover {
  transform: scale(1.1);
}
.button-decoration:hover:active {
  transform: translateY(10px);
}

.menu-item-decoration :hover {
  transform: scale(1.1);
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
  border-radius: 50px;
  padding-inline: 20px;
  padding-block: 5px;
  box-shadow: 6px 5px 0px 0px #000, 0 0 0 2px #000;
}

.menu_item-link.active {
