  border-radius: 50px;
  padding-inline: 20px;
  padding-block: 5px;
  box-shadow: 6px 5px 0px 0px #000, 0 0 0 2px #000;
}

.menu_item-link.active::after {
  content: "";
  position: absolute;
  display: block;
  background-color: black;
  width: 8px;
  height: 8px;
  border-radius: 10px;
  left: 50%;
  top: 40px;
